import React from 'react';
import {BrowserRouter as Router,Route,Link} from "react-router-dom";
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';

import './App.css';

import Footer from "./components/Footer"
import HomePage from "./pages/HomePage"
import AboutPage from "./pages/AboutPage"
import ContactPage from "./pages/ContactPage"

class App extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      title: "DapLabs",path:'/',
      headerlinks: [
        {title: 'Home', path:'/'},
        {title: 'Treasury', path:'/treasury'},
        {title: 'About Us', path:'/about'},
        {title: 'Contact', path:'/contact'}
      ],
      home: {
        title: "Keep your business up to date with the \nlatest technological advancements.",
      },
      about: {
        title: "Services provided:",
      },
      contact: { 
        title: "Contact Us",
      },
    }
  }
  render(){

    return (
      <Router>
        <Container className="p-0" fluid ={true}>

          <Navbar className="border-bottom" bg="transparent" expand="lg">
          <Link className="nav-link" to="/"><img src="./Daplabs Text Logo.jpg"/></Link>
          <Nav className="mx-auto">
          <Link className="nav-link" to="/">DapLabs</Link>

          </Nav>
              <Navbar.Toggle className="border-0" aria-controls="navbar-toggle"/>
              <Navbar.Collapse id="navbar-toggle">
              <Nav className="mx-auto">
              <Link className="nav-link" to="/">Research and Development</Link>
              <Link className="nav-link" to="/about">Services</Link>
              <Link className="nav-link" to="/about">Network Validation</Link>
              </Nav> 
              </Navbar.Collapse>
          </Navbar>
          <Route path="/" exact render={() => <HomePage  title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text}/>}/>
          <Route path="/about" exact render={() => <AboutPage  title={this.state.about.title} />}/>
          <Route path="/contact" exact render={() => <ContactPage  title={this.state.contact.title} />}/>
          <Footer/>
      
        </Container>
      </Router> 
      );  
  }  
}

export default App;
