import { render } from "@testing-library/react";
import React from "react";
import Hero from "../components/Hero";
import Content from "../components/content"
import { Button, Form, FormControl, FormGroup } from "react-bootstrap";


class ContactPage extends React.Component {

    constructor(props) {

        super(props);
        this.state = {
            name: '',
            email:'',
            message: '',
            disabled: false,
            emailSent: null,
        }
    }

    handleChange = (event) => {
        console.log(event);

        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name; 
        this.setState({
            [name]: value
        })
    }



    render(){
        return(
            <div>
            <Hero title={this.props.title} />
            <Content>
                <Form onSubmit={this.handleSubmit}>
                    <FormGroup>
                        <Form.Label htmlfor="full-name">Full Name</Form.Label>
                        <FormControl id="full-name" name="name" type="text" value={this.state.name} onChange={this.handlechange}  />
                    </FormGroup>
                    <FormGroup>
                        <Form.Label htmlfor="email">Email</Form.Label>
                        <FormControl id="email" name="email" type="email" value={this.state.email} onChange={this.handlechange}  />
                    </FormGroup>
                    <FormGroup>
                        <Form.Label htmlfor="message">message</Form.Label>
                        <FormControl id="message" name="message " as="textarea" rows="3" value={this.state.message} onChange={this.handlechange}  />
                    </FormGroup>
                    <Button className="d-inline-block" variant="primary" type="submit" disabled={this.state.disabled}>
                        Send
                    </Button>

                    {this.state.emailSent === true && <p className="d-inline success-msg">Email Sent</p>}
                    {this.state.emailSent === false && <p className="d-inline err-msg">Email Not  Sent</p>}
                </Form>
            </Content>

            </div>
            
            
        );

    }

}
export default ContactPage